import React from 'react';
import Profile from "./Profile";
import {connect} from "react-redux";
import {addPhoto, getUserProfile, getUserStatus, saveProfile, updateUserStatus} from "../../redux/profileReducer";
import {withRouter} from "react-router-dom";
import {compose} from "redux";

class ProfileContainer extends React.Component{
    
    refreshProfile(){
        let userId = this.props.match.params.userId // достем id из url
    
        if(!userId){
            userId = this.props.authorizedUserId;
            if(!userId){
                this.props.history.push("/login");
            }
        }
        this.props.getUserProfile(userId);
        this.props.getUserStatus(userId);
    }
    
    componentDidMount() {
        this.refreshProfile();
    }
    
    componentDidUpdate(prevProps, prevState, snapshot) {
        if(this.props.match.params.userId !== prevProps.match.params.userId){
            this.refreshProfile();
        }
    }
    
    render() {
        return (
            <Profile saveProfile={this.props.saveProfile}
                     isOwner={!this.props.match.params.userId} // Владелец аккаунта
                     profile={this.props.profile}
                     status={this.props.status}
                     addPhoto={this.props.addPhoto}
                     updateUserStatus={this.props.updateUserStatus}/>
        )
    }
}

let mapStateToProps =(state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    authorizedUserId: state.auth.userId,
    idAuth: state.auth.isAuth
});

export default compose(
    connect(mapStateToProps, {getUserProfile, getUserStatus, updateUserStatus, addPhoto, saveProfile}),
    withRouter
)(ProfileContainer);
