import React  from 'react';
import classes from './../ProfileInfo/ProfileInfo.module.css';
import ProfileStatusWithHooks from "../ProfileInfo/ProfileStatusWithHooks";

const ProfileDescriptions = (props) => {
    return (
        <div className={classes.description}>
            {/*Name*/}
            <div className={classes.description_name}>{props.profile.fullName}</div>
    
            {/*Status*/}
            <div className={classes.description_elem}>
                <ProfileStatusWithHooks profile={props.profile} status={props.status} updateUserStatus={props.updateUserStatus}/>
            </div>
           
            {/*<div className={classes.description_elem}>
               <div className={classes.description_title}>Looking for a job</div>
                {props.profile.lookingForAJob ? "Yes" : "No"}
            </div>*/}
            
            {/*Skills*/}
            <div className={classes.description_elem}>
                <div className={classes.description_title}>Skills: </div>
                {props.profile.lookingForAJobDescription}
            </div>
    
            {/*About me*/}
            <div className={classes.description_elem}>
                <div className={classes.description_title}>About Me</div>
                {props.profile.aboutMe || "--"}
            </div>
    
            {/*Contacts*/}
            <div>
                <div className={classes.description_name}>Contacts</div>
                {Object.keys(props.profile.contacts).map(key => {
                    return <ContactsHelper key={key} contactTitle={key} contactValue={props.profile.contacts[key]}/>
                })}
            </div>
            
            {/*Button - Settings*/}
            <div className={classes.description_button}>
                {props.isOwner && <button onClick={props.enableEditMode}>Settings</button>}
            </div>
        </div>
    )
}

const ContactsHelper = ({contactTitle, contactValue}) => {
    return <div>
        <span className={classes.contacts_title}>{contactTitle}: </span>
        {contactValue}
    </div>
}

export default ProfileDescriptions