import React from 'react';
import classes from './UserPost.module.css';
import Post from "../Messages/Post/Post";
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, requiredField} from "../../../utils/validators";
import {Textarea} from "../../Common/FormControls/FormControls";

const UserPost = props => {
    let postElements = props.postData.map(post =>
        <Post text={post.post} likeCount={"likes " + post.likesCount} key={post.id}/>
    )
    
    let addPost = (formData) => {
        props.addUserPost(formData.postItemText)
    }
    
    return (
        <div  className={classes.user}>
            {/*Posts*/}
            <div className={classes.messagesContainer}>
                {postElements}
            </div>
            
            {/* User comment */}
            <div className={classes.user_post}>
                <AddPostReduxForm onSubmit={addPost}/>
            </div>
        </div>
    )
};

const maxLength30 = maxLengthCreator(30);

const AddPostForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            
            {/*Add comment*/}
            <Field component={Textarea} name='postItemText'
                   placeholder='Type a comment' validate={[requiredField, maxLength30]}/>
        
            {/*Button - send comment*/}
            <div className={classes.user_post_button}>
                <button>Send</button>
            </div>
        </form>
    )
}

const AddPostReduxForm = reduxForm({form: 'profileAddUserPost'})(AddPostForm)

export default UserPost;