import React, {useState} from 'react';
import classes from './ProfileInfo.module.css';
import userPhoto from "../../../assets/images/img-user.png";
import ProfileEditForm from "./ProfileEditForm";
import ProfileDescription from "../ProfileDescriptions/ProfileDescriptions";

const ProfileInfo = (props) =>{
    
    let [editMode, setEditMode]  = useState(false);
    
    const onAddMainPhoto = (e) =>{
        if (e.target.files.length){
            props.addPhoto(e.target.files[0]);
        }
    }
    
    let addNewProfileData = (formData) => {
        const promise = props.saveProfile(formData);
        promise.then(() => {
            setEditMode(false)
        })
    }
    
    return(
        <div className={classes.content}>
            <div className={classes.content_profile}>
                <div >
                    {/* User avatar */}
                    <div className={classes.avatar}>
                        <img src={props.profile.photos.small || userPhoto} alt={props.profile.contacts.fullName} />
                    </div>
                </div>
                
                {/*Edit status*/}
                <div>
                    {
                        editMode
                        
                        ? <div>
                                {/* Add file */}
                                <div  className={classes.addUserPhoto}>
                                    {props.isOwner &&
                                        <label >
                                            Upload new photo
                                            <input id={'file_upload'} type={"file"} onChange={onAddMainPhoto}/>
                                        </label>
                                    }
                                </div>
                            
                                <ProfileEditForm initialValues={props.profile} onSubmit={addNewProfileData}
                                                 profile={props.profile} />
                          </div>
                          
                        : <ProfileDescription profile={props.profile} status={props.status} isOwner={props.isOwner}
                                              updateUserStatus={props.updateUserStatus}
                                              enableEditMode={() => setEditMode(true)}  />
                        
                    }
                </div>
            </div>
        </div>
    )
}

export default ProfileInfo;