import React from 'react';
import classes from  './Profile.module.css';
import ProfileInfo from "./ProfileInfo/ProfileInfo";
import UserPostContainer from "./UserPost/UserPostContainer";
import Preloader from "../Common/Preloader/Preloader";
import ProfileDescriptions from "./ProfileDescriptions/ProfileDescriptions";

const Profile = (props) =>{
    
    if(!props.profile){
        return <Preloader />
    }
    
    /*TODO: перенести все пропсы в переменные, как в примере снизу*/
    const isOwner = props.isOwner;
    const saveProfile = props.saveProfile;
    
    return(
        <div className={classes.profile}>

            {/* Профиль */}
            <ProfileInfo saveProfile={saveProfile}
                         isOwner={isOwner}
                         profile={props.profile}
                         status={props.status}
                         addPhoto={props.addPhoto}
                         updateUserStatus={props.updateUserStatus}
            />
            
        
            {/* Сообщение пользователя */}
            <UserPostContainer />

      </div>
    )
}
export default Profile;