import React from "react";
import {Input, Textarea} from "../../Common/FormControls/FormControls";
import {Field, reduxForm} from "redux-form";
import classes from "../../Common/FormControls/FormControls.module.css";
import styles from './ProfileInfo.module.css';

/*Edit information about user*/
const ProfileEditForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            
            {/*Errors*/}
            <div>
                { props.error && <div className={classes.errorSummary}> {props.error} </div> }
            </div>
            
            {/*Button - edit*/}
           {/* <div>{props.isOwner &&
                <button onClick={props.enableEditMode}>edit</button>}
            </div>*/}
    
            {/*User - name*/}
            <div>
                {/*TODO: Добавить label для всех полей форм
                 https://stackoverflow.com/questions/49392535/style-redux-form-field-label
                 */}
                <span><b>Full Name: </b></span>
                <Field component={Input} name='fullName'
                       placeholder='Full Name' validate={[]}/>
            </div>
            
            {/*User - looking for a job*/}
{/*            <div>
                <label>Looking for a job:
                <Field className={classes.test} label={'style me!!'} component={Input} name='lookingForAJob'
                       validate={[]} type={"checkbox"}
                />
                </label>
            </div>*/}
            
            {/*User - prof skills*/}
            <div>
                <b>Prof. skills: </b>
                <Field component={Textarea} name='lookingForAJobDescription'
                       placeholder='Description...' validate={[]}/>
            </div>
            
            {/*About Me*/}
            <div><b>About Me:</b>
                <Field component={Textarea} name='aboutMe'
                       placeholder='About me...' validate={[]}/>
            </div>
            
            <br/>
            
            {/*User - contacts*/}
            <div>
                {Object.keys(props.profile.contacts).map(key => {
                    return (
                        <div key={key}>
                            <b>{key}: </b>
                            {<Field component={Input} name={'contacts.' + key} placeholder={key} validate={[]}/>}
                        </div>)
                })}
            </div>
    
            {/*Button - save*/}
            <div className={styles.editProfile_button_save}>
                <button onClick={props.enableEditMode}>Save</button>
            </div>
        </form>
    )
}

const ProfileEditReduxForm = reduxForm({form: 'profileEditForm'})(ProfileEditForm)

export default ProfileEditReduxForm;